<template>
  <v-ons-page>
    <v-ons-toolbar class="home-toolbar">
      <div class="left">
        <v-ons-toolbar-button @click="$emit('toggleMenu')">
          <v-ons-icon icon="fa-bars"></v-ons-icon>
        </v-ons-toolbar-button>
      </div>
      <div class="center">{{ msg }}</div>
    </v-ons-toolbar>

    <div class="header">
      <img src="../assets/logo.png" alt="vue-logo">
    </div>

    <v-ons-list-title>Vue.js Essential Links</v-ons-list-title>
    <v-ons-list>
      <v-ons-list-item>Item B</v-ons-list-item>
      <v-ons-list-item>

        <li v-for="post in posts">
            <div>
                <a v-bind:href="post.link">
                    <h1>{{post.title.rendered}}</h1>
                </a>
            </div>
        </li>
      </v-ons-list-item>
        
    </v-ons-list>

  </v-ons-page>
</template>

<script>
import axios from 'axios';
const POST_END_POINT = 'https://code.hot-factory.jp/lunch';//適宜変更してください

export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome',
      posts: []
    }
  },
  methods: {
    goTo (url) {
      const newWindow = window.open(url, '_blank')
      newWindow.opener = null
      newWindow.location = url
    }
  },
  created(){
      axios.get ('https://code.hot-factory.jp/lunch/wp-json/wp/v2/store')
        .then ((response) => {
            this.posts = response.data;
            // console.log('TEST'+response.data.title);
        }).catch( ( res ) => {
                    console.error( res );
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}
</style>



